
import React, {Component} from 'react';
import Iframe from '../../components/iframe/iframe';
import './Lionfish.css';


    class Lionfish extends Component {
    constructor(props) {
        super(props);
        this.state = {
            src: 'https://oceanservice.noaa.gov/caribbean-mapping/video/lionfish.mp4'
        };
    }

    render() {
        return (
            <div className="vz">
                <Iframe className = "vz"source={this.state.src} />
            </div>
        );
    }
}

export default Lionfish;