class Atmosphere extends Component{


  state = {
    sideDrawerOpen:false
  };

drawerToggleClickHandler = ()=> {

  this.setState((prevState) =>{
    return{sideDrawerOpen: !prevState.sideDrawerOpen};
});
};

backdropClickHandler = () => {
    this.setState({sideDrawerOpen:false});
};

  render(){

   
    let backdrop;

    if (this.state.sideDrawerOpen){
   
      backdrop= <Backdrop click={this.backdropClickHandler}/>
    }
    return(
      
        <body> 
          <Toolbar drawerClickHandler={this.drawerToggleClickHandler} />
          <SideDrawer show={this.state.sideDrawerOpen} />
          {backdrop}
          <SideMenuAir />
          <AirBg/>
          
          <main className = "bod">
          
          
          
          <Contentwrapper>
          <Tree>Atmosphere /</Tree>

          </Contentwrapper>

          
          
          <Lower>
            <VitalContainer>
              <Link to="/Land_Deforestation">
              <Vitalwrapperlft><VitalDeforestation/></Vitalwrapperlft>
              </Link>
              <Vitalwrapperrt><a href= "/"><VitalCO2/></a></Vitalwrapperrt>
            </VitalContainer>
            <VitalContainer>
              <Vitalwrapperlft><a href= "/"><VitalSealv/></a></Vitalwrapperlft>
              <Vitalwrapperrt><a href= "/"><VitalSealv/></a></Vitalwrapperrt>
            </VitalContainer>
          </Lower>
         
                  
          </main>
        </body>
      
      );

  } 
}

export default Atmosphere;