class App extends Component{


  state = {
    sideDrawerOpen:false
  };

drawerToggleClickHandler = ()=> {

  this.setState((prevState) =>{
    return{sideDrawerOpen: !prevState.sideDrawerOpen};
});
};

backdropClickHandler = () => {
    this.setState({sideDrawerOpen:false});
};

  render(){

   
    let backdrop;

    if (this.state.sideDrawerOpen){
   
      backdrop= <Backdrop click={this.backdropClickHandler}/>
    }



    return(
      
      <Router>
      <ScrollToTop>
        <body> 
         
          <Toolbar drawerClickHandler={this.drawerToggleClickHandler} />
          <SideDrawer show={this.state.sideDrawerOpen} />
          {backdrop}
          <SideMenuHome />
          <HomeBg/>
          <main className = "contentmain">

          <Switch>
              <Route path= "/" component={Home} exact/>
              <Route path= "/Oceans" component ={Oceans}/>
              <Route path= "/Oceans_Aquatic-life" component ={Ocean_Aquaticlife}/>
              <Route path= "/Oceans_Arctic" component ={Ocean_Arctic}/>
              <Route path= "/Oceans_Physical-properties" component ={Ocean_Physicalproperties}/>
              <Route path= "/Oceans_Sea-Level" component={Ocean_SeaLevel}/>
              <Route path= "/Oceans_Acidification" component={Ocean_Acidification}/>
              <Route path= "/Oceans_Arctic-ice-minimum" component={Ocean_Arcticicemin}/>
              <Route path= "/Oceans_Plastic-pollution" component={Ocean_Plasticpollution}/>
              <Route path= "/Oceans_Lionfish" component={Ocean_Lionfish}/>
              <Route path= "/Oceans_Coral-reefs" component={Ocean_Coral}/>
              <Route path= "/Ocean_Sea-turtle" component={Ocean_Seaturtle}/>
              <Route path= "/Land" component= {Land}/>
              <Route path= "/Land_Deforestation" component={Land_Deforestation}/> 
              <Route path= "/Land_Forestfires" component={Land_Forestfires}/> 
              <Route path= "/Atmosphere" component={Atmosphere}/>       
              <Route path= "/Near-Space" component={NearSpace}/>     
          
          </Switch>

          </main>
        </body>
        </ScrollToTop>
      </Router>
      
      );

  } 
}


function Home() {
  return (
    <body> 
      <main className = "bod">

      <Contentwrapper>
          <Tree>Home/</Tree>         
      </Contentwrapper>
      
      <Upper>
            
          <Contentwrapper>
            <Threat>          
              <InfoEarthnight/>
            </Threat>
          </Contentwrapper>
     
     <Contentwrapper>
          <Impacts>
              <Lower>   
                    <Vitalwrapperlft><Link to="/Oceans"><VitalOceans/></Link></Vitalwrapperlft>
                    <Vitalwrapperrt><Link to="/Land"><VitalLand/></Link></Vitalwrapperrt>  
              </Lower>

              <Lower>   
                    <Vitalwrapperlft><Link to="/Atmosphere"><VitalAir/></Link></Vitalwrapperlft>
                    <Vitalwrapperrt><Link to="/Near-Space"><VitalNearspace/></Link></Vitalwrapperrt>  
              </Lower>

            <Impactwrapper><InfoAffected/></Impactwrapper>  
          </Impacts>
      </Contentwrapper>
      
      </Upper>
          

      </main>
    </body>
)
}

export default App;





